import IndexProductsPage from '../Pages/Product/Admin/IndexProductsPage';
import CreateProductPage from '../Pages/Product/Admin/CreateProductPage';
import EditProductPage from '../Pages/Product/Admin/EditProductPage';
import DeleteProductPage from '../Pages/Product/Admin/DeleteProductPage';
import PublicRoutes from './PublicRoutes';

import {
  Switch,
  Route
} from "react-router-dom"

/**
 * Rutas para la administracion de 
 * los productos
 * 
 * @returns rutas de administracion
 */
const AdminRoutes = () => {
  return (
    <Switch>
      <Route path="/admin/products" component={IndexProductsPage}/>
      <Route path="/admin/product/create" component={CreateProductPage}/>
      <Route path="/admin/product/edit/:id" component={EditProductPage}/>
      <Route path="/admin/product/delete/:id" component={DeleteProductPage}/>
      <PublicRoutes/>
    </Switch>
  );
}
export default AdminRoutes;
