import React, { useState, useEffect } from 'react';
import CartContext from "./CartContext";

const CartProvider = (props) => {
    /**
     * Comprueba si existe un carrito en
     * el localStorage si es asi lo parsea a JSON 
     * sino retorna un array vacio
     * 
     * @returns cart (local)
     */
    const cartInLocal = () => {
        return (localStorage.getItem('cart') !== null) ? JSON.parse(localStorage.getItem('cart')) : [];
    };
    /**
     * Comprueba si existe pedidos de carritos en
     * el localStorage si es asi lo parsea a JSON 
     * sino retorna un array vacio
     * 
     * @returns cartOrders (local)
     */
    const cartOrdersInLocal = () => {
        return (localStorage.getItem('cartOrders') !== null) ? JSON.parse(localStorage.getItem('cartOrders')) : [];
    };
    //Hooks con sus valores iniciales
    const [cart, setCart] = useState(cartInLocal);
    const [qtyProducts, setQtyProducts] = useState(0);
    const [totalCartPay, setTotalCartPay] = useState(0);
    const [orderIds, setOrderIds] = useState(cartOrdersInLocal);
    
    //componentDidMount
    useEffect(() => {
        updateQtyProducts();
        localStorage.setItem('cart', JSON.stringify(cart));
        localStorage.setItem('cartOrders', JSON.stringify(orderIds));
        totalPay();
    });
    /**
     * 
     * @param {*} addProduct
     */
    const addProductToCart = (addProduct) => {
        //buscamos si el producto ya esta en el carrito
        const findProduct = cart.find(product => product.id === addProduct.id);
        /**
         * Si el producto esta en el carrito buscamos el producto
         * asi actualizamos la cantidad.
         * Si no se encuentra en el carrito lo agregamos
         */
        if (findProduct !== undefined) {
            //buscamos el index del producto a agregar
            const indexFindProduct= cart.findIndex(product => product.id === addProduct.id);
            /**
             * sacamos el producto del carrito 
             * asi sumamos la cantidad recibida 
             * con la que ya tenia
             * asi no se duplican en el local storage
             */
            cart.splice(indexFindProduct, 1,
                {
                    sku: addProduct.sku,
                    title: addProduct.title,
                    quantity: addProduct.quantity + findProduct.quantity, //sumamos la cantidad que tenia
                    price: addProduct.price,
                    image: addProduct.image,
                    id: addProduct.id,
                    category: addProduct.category
                }
            );

        } else {
            setCart([
                //destructured
                ...cart,
                {
                    sku: addProduct.sku,
                    title: addProduct.title,
                    quantity: addProduct.quantity,
                    price: addProduct.price,
                    image: addProduct.image,
                    id: addProduct.id,
                    category: addProduct.category
                }
            ]);
        }
    };

    /**
     * Comprueba si el producto esta en el carrito
     * lo busca por sku
     * @param {*} idProducto 
     * @returns true or false
     */
    const isInCart = (idProducto) => {
        let findProduct = cart.find(product => product.id === idProducto);
        return findProduct;
    };
    /**
     * Actualiza la cantidad Productos 
     * que tenemos el carrito
     */
    const updateQtyProducts = () => {
        //Aplicamos reduce para acumular las cantidad de cada producto en el carrito
        let totalProducts = cart.reduce((acc, item) => acc + item.quantity, 0);
        setQtyProducts(totalProducts);
    };

    /**
     * Total a pagar 
     * Suma todos los precios de los productos en el carrito
     */
    const totalPay = () => {
        //Aplicamos reduce para acumular el precio * la cantidad de cada producto en el carrito 
        const sumPay = cart.reduce((acc, prod) => acc + prod.price * prod.quantity, 0);
        setTotalCartPay(sumPay);
    };
    /**
     * Busca el producto por el SKU
     * y lo filtra
     * @param {*} idProducto
     */
    const deleteProduct = (idProducto) => {
        //Retornamos todos los productos distinto al SKU que recibe
        const editedProducts= cart.filter(product => product.id !== idProducto);
        setCart(editedProducts);
        //volvemos al setear el cart
        localStorage.setItem('cart', JSON.stringify(editedProducts));
    };

    return (
        <CartContext.Provider value={{
            addProductToCart,
            isInCart,
            cart,
            setCart,
            qtyProducts,
            updateQtyProducts,
            totalCartPay,
            deleteProduct,
            orderIds,
            setOrderIds,
            cartInLocal,
            cartOrdersInLocal
        }}>
            {/** Mostramos los componentes hijos*/}
            {props.children}
        </CartContext.Provider>
    )
}
export default CartProvider;



