import HomePage from '../Pages/Home/HomePage';
import DetailPage from '../Pages/Product/DetailPage';
import RegisterPage from '../Pages/Auth/RegisterPage';
import LoginPage from '../Pages/Auth/LoginPage';
import LogoutPage from '../Pages/Auth/LogoutPage';
import CartPage from '../Pages/Cart/CartPage';
import CartCheckoutPage from '../Pages/Cart/CartCheckoutPage';
import OrdersPage from '../Pages/Orders/OrdersPage';
import NotFoundPage from '../Pages/Error/NotFoundPage';
import NotFoundProductPage from '../Pages/Error/NotFoundProductPage';
import {
  Switch,
  Route,
  Redirect,
} from "react-router-dom"

/**
 * Rutas que son de acceso publico
 * 
 * @returns rutas publicas
 */
const PublicRoutes = () => {
  return (
      <Switch>
        <Route path="/register" component={RegisterPage}/>
        <Route path="/login" component={LoginPage}/>
        <Route path="/logout" component={LogoutPage}/>
        <Route path="/product/detail/:id"  component={DetailPage}/>
        <Route path="/cart" component={CartPage}/>
        <Route path="/checkout" component={CartCheckoutPage}/>
        <Route path="/orders" component={OrdersPage}/>
        <Route path="/notfound/product" component={NotFoundProductPage}/>
        <Redirect from="/home" to="/"/>
        <Route exact path="/" component={HomePage}/>
        <Route path="*" component={NotFoundPage}/>
      </Switch>
  );
}

export default PublicRoutes;
