import { React, useState, useEffect, useContext } from "react"
import { numberFormatCurrency } from '../../Config/format';
import { FaTrash } from "react-icons/fa";
import { Button } from 'react-bootstrap'
import firebase from '../../Config/firebase';

//Context Cart
import { CartContext } from '../../Context/Cart/CartContext';
/**
 * Listado de Productos
 * @returns 
 */
const OrdersPage = () => {
    const [ordersInfo, setOrdersInfo] = useState([])
    //Context Cart
    const { orderIds, setOrderIds } = useContext(CartContext);
    /**
     * 
     */
    useEffect(
        () => {
            const request = async () => {
                try {
                    //Obtenemos todas las ordenes con los id locales
                    const orderRefs = orderIds.map(async ({ id }) => {
                        return await firebase.dB.collection('pedidos').doc(id).get();
                    });
                    //Guardamos todos los documentos
                    const docs = await Promise.all(orderRefs)
                    //recorremos los pedidos
                    const orders = docs.map(doc => ({ id: doc.id, ...doc.data() }));
                    //Reverse asi el ultimo pedidos es el primero
                    setOrdersInfo(orders.reverse())
                    console.log(orders);
                } catch (error) {
                    console.log(error);
                }
            }
            request()
        }, [orderIds])

    return (
        <>
            <div className="row">
                <div className="col-md-4 col-xs-4 col-sm-4 mx-auto">
                    <div className="card card-body bg-light mt-5 text-center">
                        <h3 className="card-title">Mis Pedidos</h3>
                        <Button disabled={orderIds.length === 0} onClick={()=> {
                            localStorage.removeItem('cartOrders');
                            setOrderIds([])
                        }} className="btn-block" variant="success" size="lg"> Limpiar Pedidos <FaTrash /></Button>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12 col-xs-12 col-sm-12">
                    <div className="card card-body bg-light mt-5">
                        <div className="col-md-12 table-responsive-sm">
                            <table className="table table-hover text-center">
                                <thead className="thead-dark">
                                    <tr>
                                        <th scope="col" className="col-1">#</th>
                                        <th scope="col" className="col-2">Cod. Referencia</th>
                                        <th scope="col" className="col-2">Fecha</th>
                                        <th scope="col">Productos</th>
                                        <th scope="col" className="col-2">Total</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {ordersInfo &&
                                        ordersInfo.map(({ id, total, date, productos },index) => (
                                            <tr key={id}>
                                                <td>{(index + 1)}</td>
                                                <td>{id}</td>
                                                <td>{date.toDate().toLocaleDateString('es-AR')}</td>
                                                <td>
                                                    <ul>
                                                    {
                                                        productos &&
                                                        productos.map(producto => <li key={producto.id}> {producto.title} x {producto.quantity} </li> )
                                                    }
                                                    </ul>
                                                </td>
                                                <td>{numberFormatCurrency(total)}</td>
                                            </tr>
                                        ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default OrdersPage;