import { React } from "react";
import { Button } from 'react-bootstrap'
import { FaTrash } from "react-icons/fa";
import { numberFormatCurrency } from "../../Config/format";

const Detail = ({ idProducto, producto, index, deleteProduct }) => {
    return (
        <tr>
            <th scope="row">{index+1}</th>
            <td>{producto.title}</td>
            <td>{producto.quantity}</td>
            <td>{numberFormatCurrency(producto.price)}</td>
            <td>{numberFormatCurrency(producto.price * producto.quantity)}</td>
            <td><Button variant="outline-danger" size="sm" onClick={() => deleteProduct(idProducto)}><FaTrash/></Button></td>
        </tr>
    );
}
export default Detail;