import { React, useState, useEffect } from "react";
import { Alert } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap'
/**
 * 
 * @param {*} props 
 * @returns 
 */
const AlertDismissible = ({ alert }) => {
  const [show, setShow] = useState(false);
  //cuando surge un cambio en el props
  useEffect(
    () => {
      setShow(alert.show)
    }, [alert]
  );
  return (
    <>
      {
       //comprobamos si show es true caso contrario retornamos null
        show ?
          <Alert variant={alert.variant} onClose={() => setShow(false)} dismissible>
            <Alert.Heading>¡{alert.heading}!</Alert.Heading>
            <p>
              {alert.message}
              {alert.link && <LinkContainer to={alert.linkTo}><Alert.Link>{alert.linkText}</Alert.Link></LinkContainer>}
            </p>
          </Alert>
          :
          null
      }
    </>
  );
}
export default AlertDismissible;
