import {React,useContext,useState ,useEffect, useCallback} from 'react';
import { Button, InputGroup } from 'react-bootstrap'
//Context Cart
import {CartContext} from '../../Context/Cart/CartContext';
import { FaCartPlus,FaPlusCircle, FaMinusCircle } from 'react-icons/fa';

/**
 * Contador de Productos
 * 
 * @param {object} producto 
 * @returns 
 */
const Counter = ({producto, idProducto, alert, setAlert}) => {

    const styleInput ={
        textAlign:"center",
    }

    //Context
    const {addProductToCart,isInCart,updateQtyProducts} = useContext(CartContext);
    //Valor Inicial del Contador si no hay stock lo dejamos en "No Tenemos Stock"
    const initial = (producto.stock === 0) ? "No Tenemos Stock" : 1;
    const [counter, setCounter] = useState(initial);
    //Hooks que controla si el producto ya esta en el carrito
    const [isIn,setIsIn] = useState();
    /**
     * Hooks con el que controlamos que la suma de los productos que ya estan carrito
     * y los que se van a agregar no superen el stock del producto
     */
    const [limitStock,setLimitStock] = useState();
    //Cuando Presionamos para disminuir la cantidad de producto
    const handleDwnQty = () =>{ 
        setCounter(counter -1);
    }
    //Cuando Presionamos para aumentar la cantidad de producto
    const handleUpQty = () => {
        setCounter(counter + 1);
    }
    /**
     * Comprobamos si el producto esta en el carrito
     * si lo esta nos devuelve el producto
     * Callback memorizado que solo cambia cuando sus dependencias cambia
     */
    const isProductInCart = useCallback(() => isInCart(idProducto),[isInCart, idProducto]);
    
    /**
     * Comprobamos si el producto que esta en el carrito no supera el stock
     * para poder setear los hooks
     * Callback memorizado que solo cambia cuando sus dependencias cambia
     */
    const limitQtyStock = useCallback(() => {
        //Producto en el carrito
        const productInCart = isProductInCart();
        /**
         * si es que esta en el carrito comprobamos las cantidades con el stock
         * si es igual llegamos al limite y seteamo el limite en true sino false
         */
        ((productInCart?.quantity + counter) === Number(producto.stock)) ? setLimitStock(true) : setLimitStock(false);
    },[counter, isProductInCart,producto.stock]) 
    
    /**
     * Al Hacer Click en el boton agregar al 
     * carrito
     */
    const handleClickAddToCart =()=>{
        addProductToCart(
            {
                sku: producto.sku,
                title: producto.title,
                quantity: counter,
                price: producto.price,
                image: producto.image,
                id: idProducto,
                category: producto.category
            }
        );
        setCounter(initial);
        updateQtyProducts();
        setAlert({
            ...alert,
            message:'Agregamos el Producto a Tu Carrito.',
            show:true
        })
    }
    /**
     * Solo cuando cambia
     * isProductInCart, limitQtyStock
     */
    useEffect(() => {
        setIsIn(isProductInCart);
        limitQtyStock();
    }, [isProductInCart, limitQtyStock,isIn])

    return (
        <>
            <div className="row">
                <div className="col">
                <InputGroup size="lg">
                    <Button onClick = { handleDwnQty } disabled={ counter <= initial } variant="danger" ><FaMinusCircle/> </Button>
                    <input style={styleInput} onChange = { isProductInCart } className="form-control input-lg" value={
                            (isIn !== undefined && isIn?.quantity === producto.stock ) ?
                            "No Tenemos más Stock"
                            :
                            counter 
                        } readOnly/>
                    <Button onClick = { handleUpQty } 
                     disabled={ counter >= producto.stock || producto.stock === 0 || (isIn !== undefined && isIn?.quantity === producto.stock ) || limitStock } variant="success">
                        <FaPlusCircle/>
                    </Button>
                </InputGroup>
                </div>
                <div className="col">
                    {/**Deshabilito si se paso el stock*/ }
                    <Button onClick={handleClickAddToCart} className="btn-block" variant="success" size="lg"
                        disabled={producto.stock === 0 || (isIn !== undefined && isIn?.quantity === producto.stock )}
                    >
                        {
                            /**
                             * Si distinto de undefined ya esta en el carrito
                             * entonces cambiamos el mensaje del boton
                             */
                            isIn !== undefined  ? 
                            `Te Agregamos ${counter} más ` 
                            : 
                            ' Te Agregamos al Carrito'
                        }
                        <FaCartPlus/>
                    </Button>
                </div>
            </div>
        </>
    )
}
export default Counter
