import { React, useContext } from "react"
import Detail from '../../Components/Cart/Detail';
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
//Context Cart
import { CartContext } from '../../Context/Cart/CartContext';
//Context Auth
import { AuthContext } from '../../Context/Auth/AuthContext';
import { numberFormatCurrency } from "../../Config/format";
/**
 * Listado del Carrito Local
 * @returns 
 */
const CartPage = () => {
    //Context Auth
    const { userIsLogin } = useContext(AuthContext)
    //Context Cart
    const { cart, totalCartPay, deleteProduct } = useContext(CartContext);
    return (
        <div className="row">
            <div className="col-md-8 col-xs-12 col-sm-12">
                <div className="card card-body bg-light mt-5">
                    <h3>{cart.length === 0 ? 'Tu Carrito está Vacío' : 'Productos en Tu Carrito' }</h3>
                    <div className="col-md-12 table-responsive-sm">
                        <table className="table table-hover">
                            <thead>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Producto</th>
                                    <th scope="col">Cantidad</th>
                                    <th scope="col">Precio Uni.</th>
                                    <th scope="col">Total</th>
                                    <th scope="col">Quitar</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    cart.map((producto, index) => {
                                        return <Detail key={producto.id} index={index} idProducto={producto.id} producto={producto} deleteProduct={deleteProduct} />
                                    })
                                }

                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            {
                cart.length > 0 &&
                <div className="col-md-4 col-xs-8 col-sm-8">
                    <div className="card card-body bg-light mt-5">
                        <h3>Finalizar Pedido</h3>
                        <p className="font-weight-bold">Total de Tu Pedido: {numberFormatCurrency(totalCartPay)}</p>
                        {
                            userIsLogin ?
                                <LinkContainer to="/checkout">
                                <Button className="btn-block" variant="success" size="lg">
                                Finalizar Pedido
                                </Button>
                            </LinkContainer>
                                :
                                <LinkContainer to="/login">
                                    <Button className="btn-block" variant="warning" size="lg">
                                        Debes Iniciar Sesión
                                    </Button>
                                </LinkContainer>
                        }
                    </div>
                </div>
            }
        </div>
    )
}
export default CartPage