import React, { useState } from "react";
import AuthContext from "./AuthContext";

/**
 * Proveedor de Contexto Auth
 * 
 * @param {*} props 
 * @returns 
 */
const AuthProvider = (props) => {
  //Hooks usuario logueado y datos de usuario
  const [userIsLogin, setUserIsLogin] = useState(localStorage.getItem("userIsLogin"));
  const [userInfo, setUserInfo] = useState(JSON.parse(localStorage.getItem("userInfo")));

  /**
   * Inicio de sesion de usuario
   * setea los hooks userIsLogin a true 
   * y userInfo con la informacion del usuario
   * 
   * @param {*} userInfo 
   */
  const loginUser = (userInfo) => {
    localStorage.setItem("userIsLogin", true);
    localStorage.setItem("userInfo", JSON.stringify(userInfo));
    setUserIsLogin(true);
    setUserInfo(userInfo);
  };
  /**
   * Cierre de sesion de usuario
   * setea los hooks userIsLogin a false 
   * y userInfo clo deja vacio
   * 
   */
  const logoutUser = () => {
    localStorage.removeItem("userIsLogin");
    localStorage.removeItem("userInfo");
    setUserIsLogin(false);
    setUserInfo(false);
  };


  return (
    <AuthContext.Provider
      value={{
        userIsLogin,
        loginUser,
        logoutUser,
        userInfo,
      }}
    >
      {/** Mostramos los componentes hijos*/ }
      {props.children}
    </AuthContext.Provider>
  );
}
export default AuthProvider;
