import { React, useContext, useState, useEffect, useCallback } from "react"
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
//Context Cart
import { CartContext } from '../../Context/Cart/CartContext';
//Context Auth
import { AuthContext } from '../../Context/Auth/AuthContext';
import { numberFormatCurrency, numberFormat } from "../../Config/format";
import AlertDismissible from "../../Components/Utils/AlertDismissible";
//Firebase
import firebase from '../../Config/firebase';
import {
    Redirect,
} from "react-router-dom"
/**
 * Formulario de CheckoutPage
 * @returns 
 */
const CartCheckoutPage = () => {
    //Context Auth
    const { userIsLogin, userInfo } = useContext(AuthContext)
    //Context Cart
    const { cart, setCart, totalCartPay, qtyProducts, orderIds, setOrderIds, cartInLocal, deleteProduct } = useContext(CartContext);
    //Hook Productos
    const [productos, setProductos] = useState([])
    //Datos inicial alert
    const initialAlert = {
        show: false,
        message: '',
        variant: 'success',
        heading: 'Exito'
    }
    //Hooks Alert
    const [alert, setAlert] = useState(initialAlert);

    /**
     * Buscamos el producto
     * si lo esta nos devuelve el producto
     * Callback memorizado que solo cambia cuando sus dependencias cambia
     */
    const searchProduct = useCallback((idProducto) => {
        const findProduct = productos.find(producto => producto.id === idProducto);
        return findProduct;
    }, [productos]);

    /**
     * Validamos el stock si es menor al pedido lo sacamos de carrito
     * 
     * Callback memorizado que solo cambia cuando sus dependencias cambia
     */
    const validateStock = useCallback(() => {
        cart.forEach(cartProduct => {
            const currentProductId = searchProduct(cartProduct.id)?.id;
            const currentProductStock = searchProduct(cartProduct.id)?.data().stock;
            if (currentProductStock < cartProduct.quantity) {
                deleteProduct(currentProductId)
                setAlert({
                    show: true,
                    message: 'Sacamos algunos Productos de Tu Pedido ya que no Cantabamos con el Stock Suficiente.',
                    variant: 'danger',
                    heading: 'Ups'
                })
            }
        })
    }, [cart, searchProduct, deleteProduct]);

    useEffect(
        () => {
            async function request() {
                try {
                    const querySnapshot = await firebase.dB.collection("productos")
                        .get()
                    if (querySnapshot.docs) {
                        setProductos(querySnapshot.docs)
                    }
                } catch (e) {
                    console.log(e);
                }
            }
            request()
        }, [])

    useEffect(
        () => {
            validateStock()
        }, [validateStock]
    );
    /**
     * Actualiza el stock de los producto
     */
    const updateStocks = async () => {
        try {

            const document = await firebase.dB.collection('productos');
            const batch = firebase.dB.batch()

            //recorremos cart para actualizar el stock
            try {
                cart.forEach(async (producto) => {
                    const documentRef = document.doc(producto.id)
                    await batch.update(documentRef,'stock',firebase.firestore.FieldValue.increment((- producto.quantity)))

                })
                await batch.commit()
                setAlert({
                    ...alert,
                    show: true,
                    message: 'Ya recibimos tu Pedido en Breve nos pondremos en Contacto.',
                    link:true,
                    linkTo:'/orders',
                    linkText:'Ir a Mis Pedidos'
                })
            } catch (e) {
                console.log(e);
            }
        } catch (e) {
            console.log(e);
        }


    }
    /**
     * Genera el pedido y lo guarda en 
     * la base de datos
     * 
     */
    const handleOrder = async () => {
        //Datos del pedido
        const order =
        {
            cliente: {
                nombre: userInfo.nombre,
                apellido: userInfo.apellido,
                email: userInfo.email
            },
            productos: cart.map(producto => ({
                id: producto.id,
                title: producto.title,
                price: producto.price,
                quantity: producto.quantity
            })),
            date: firebase.firestore.Timestamp.fromDate(new Date()),
            total: totalCartPay
        };
        //Limpiamos el carrito local
        localStorage.removeItem('cart');
        //Seteamos el carrito 
        setCart(cartInLocal);
        try {
            //Creamos la coleccion
            const document = await firebase.dB.collection("pedidos").add(order);
            //guardamos el id en local
            setOrderIds([...orderIds, { id: document.id }]);
            updateStocks();
        } catch (e) {
            console.log(e);
        }
    };
    //si no esta logueado 
    if (!userIsLogin) {
        return <Redirect to="/" />
    }
    return (
        <>

            <div className="col-md-8 col-xs-8 col-sm-8 mx-auto">
                <div className="card card-body bg-light mt-5">
                    <AlertDismissible alert={alert} />
                    <h3>Confirmar Pedido</h3>
                    <p className="font-weight-bold">Pedido a Nombre: {`${userInfo.apellido} ${userInfo.nombre}`}</p>
                    <p className="font-weight-bold">Email de Contacto: {userInfo.email}</p>
                    <p className="font-weight-bold">Cantidad de Productos: {numberFormat(qtyProducts)}</p>
                    <p className="font-weight-bold">Total: {numberFormatCurrency(totalCartPay)}</p>
                    <div className="row">

                        <div className="col">
                            <Button disabled={cart.length === 0} onClick={handleOrder} className="btn-block" variant="success" size="lg"> Confirmar</Button>
                        </div>
                        <div className="col">
                            <LinkContainer to="/cart"><Button className="btn-block" variant="danger" size="lg"> Cancelar</Button></LinkContainer>
                        </div>

                    </div>

                </div>
            </div>
        </>
    )
}
export default CartCheckoutPage