import { React } from 'react';
import { NavDropdown} from 'react-bootstrap';
import AuthContext from "../../Context/Auth/AuthContext";

import {
    Link,
    NavLink,
} from "react-router-dom"
import CartWidget from '../../Components/Utils/CartWidget';

const Navbar = () => {
    return (
        <>
            <AuthContext.Consumer>
                {
                    context =>
                        <>
                            <nav className="navbar sticky-top navbar-expand-lg navbar-dark bg-dark">
                                <NavLink className="navbar-brand" to="/">E-commerce</NavLink>
                                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="navbar-toggler-icon"></span>
                                </button>

                                <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
                                    <ul className="navbar-nav mr-auto">
                                        <li className="nav-item">
                                            <NavLink className="nav-link" to="/" activeClassName="active">Home</NavLink>
                                        </li>
                                        {(context.userIsLogin && context.userInfo?.admin) &&
                                            <li className="nav-item">
                                                <NavLink className="nav-link" to="/admin/products" activeClassName="active">Administrar Productos</NavLink>
                                            </li>
                                        }

                                    </ul>
                                    <ul className="navbar-nav mr-right">
                                        {(!context.userIsLogin) &&
                                            <>
                                                <li className="nav-item">
                                                    <NavLink className="nav-link" to="/login" activeClassName="active">Iniciar Sesión</NavLink>
                                                </li>
                                                <li className="nav-item">
                                                    <NavLink className="nav-link" to="/register" activeClassName="active">Registrarme</NavLink>
                                                </li>
                                            </>
                                        }
                                        {context.userIsLogin &&
                                            <NavDropdown title={context.userInfo?.nombre} variant="dark">
                                                <NavDropdown.Item as={Link} to="/orders">Mis Pedidos</NavDropdown.Item>
                                                <NavDropdown.Item as={Link} to="/logout">Cerrar Sesión</NavDropdown.Item>
                                            </NavDropdown>
                                        }
                                    </ul>
                                    <CartWidget/>
                                </div>
                            </nav>
                        </>
                }

            </AuthContext.Consumer>
        </>
    );
}
export default Navbar;