import { React,useState } from 'react';
import { numberFormatCurrency, numberFormat } from '../../Config/format';
import Counter from '../Utils/Counter';
//Particular CSS
import './Detail.css';
import AlertDismissible from "../../Components/Utils/AlertDismissible";

/**
 * Componente que muestra el detalle de un producto
 * @returns 
 */
const Detail = ({ producto }) => {
    const styleCardImage = {
        height: "300px",
        width: "300px",
        display: "block",
    };
    //Datos inicial alert
    const initialAlert = {
        show: false,
        message: '',
        variant: 'success',
        heading: 'Exito'
    }
    //Hooks Alert
    const [alert, setAlert] = useState(initialAlert);
    return (
        <>
            <div className="col-12 mt-3">
            <AlertDismissible alert={alert} />
                <div className="card">
                    <div className="card-horizontal">
                        <div className="img-square-wrapper">
                            <img src={producto.data().image} alt="imagen producto" style={styleCardImage} />
                        </div>
                        <div className="card-body">
                            <h4 className="card-title">{producto.data().title}</h4>
                            <p className="card-text text-capitalize font-weight-bold">SKU: {producto.data().sku}</p>
                            <p className="card-text font-weight-bold">Detalle: {producto.data().description}</p>
                            <p className="card-text text-capitalize font-weight-bold">Categoria: {producto.data().category}</p>
                            <p className="card-text text-capitalize font-weight-bold">Precio: {numberFormatCurrency(producto.data().price)}</p>
                            <p className="card-text text-capitalize font-weight-bold">Stock: {numberFormat(producto.data().stock)}</p>
                        </div>
                    </div>
                    <div className="card-footer">
                        <Counter setAlert={setAlert} alert={alert} producto={producto.data()} idProducto={producto.id} />
                    </div>
                </div>
            </div>
        </>
    );
}
export default Detail;