/**
 * Formatea el numero a formato moneda ARS
 * 
 * @param {*} value 
 * @returns value
 */
export const numberFormatCurrency = (value) =>
  new Intl.NumberFormat('es-AR', {
    style: 'currency',
    currency: 'ARS'
  }).format(value);

/**
 * Formatea el numero 
 * 
 * @param {*} value 
 * @returns value
 */
 export const numberFormat = (value) =>
 new Intl.NumberFormat('es-AR', {maximumSignificantDigits: 2}).format(value);